@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-main);
  color: var(--primary-color);
  background-color: var(--bg-light);
  line-height: 1.6;
  margin: 0;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: inherit;
  transition: 0.3s;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  max-width: 100%;
  display: block;
}

:root {
  --accent-rgb: 230, 172, 0;
  --primary-color: #2c3e50;
  --accent-color: #e6ac00;
  --accent-hover: #c99600;
  --bg-light: #f8f9fa;
  --white: #ffffff;
  --text-grey: #6c757d;
  --shadow-soft: 0 10px 30px rgba(0, 0, 0, 0.08);
  --shadow-hover: 0 20px 40px rgba(0, 0, 0, 0.15);
  --radius: 12px;
  --font-main: 'Inter', sans-serif;
}

h1, h2 {
  color: var(--primary-color);
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 1rem;
  text-transform: uppercase;
}

h1 {
  font-size: 3.5rem;
  font-weight: 700;
  letter-spacing: -0.5px;
  color: var(--bg-light);
}

h2 {
  font-size: 2.5rem;
  font-weight: 800;
}

h3 {
  font-size: 1.5rem !important;
  font-weight: 700;
}

h4 {
  font-size: 1rem;
  font-weight: 700;
  line-height: 1.6;
  margin-bottom: 1rem;
  color: var(--primary-color);
  margin-bottom: 1rem;
}

p {
  font-size: 1.25rem !important;
  color: var(--primary-color);
  line-height: 1.6;
  margin-bottom: 1rem;
}

strong {
  font-weight: 700;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(230, 172, 0, 0.7);
  }
  70% {
    box-shadow: 0 0 0 15px rgba(230, 172, 0, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(230, 172, 0, 0);
  }
}
.reveal {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.8s ease-out;
}
.reveal.active {
  opacity: 1;
  transform: translateY(0);
}

header {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.05);
  position: sticky;
  top: 0;
  z-index: 1000;
  padding: 20px 0;
  width: 100%;
}
header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px;
}

.logo {
  flex-shrink: 0;
}
.logo img {
  display: block;
  max-width: 150px;
  width: 100%;
  height: auto;
}
@media (max-width: 576px) {
  .logo img {
    max-width: 130px;
  }
}

.burger-btn {
  display: none;
  background: none;
  border: none;
  font-size: 1.8rem;
  cursor: pointer;
  color: var(--primary-color);
  padding: 5px;
  z-index: 1002;
  position: relative;
  margin-left: 8.5rem;
}
@media (max-width: 992px) {
  .burger-btn {
    display: block;
  }
}

.nav-menu {
  display: flex;
  gap: 40px;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (max-width: 1100px) {
  .nav-menu {
    gap: 25px;
  }
}
@media (max-width: 992px) {
  .nav-menu {
    position: fixed;
    top: 0;
    right: -100%;
    width: 320px;
    height: 100vh;
    background: white;
    flex-direction: column;
    padding: 100px 30px 30px;
    gap: 20px;
    transition: right 0.4s ease;
    box-shadow: -5px 0 20px rgba(0, 0, 0, 0.1);
    overflow-y: auto;
    z-index: 1001;
    max-width: 90vw;
  }
  .nav-menu.active {
    right: 0;
    display: flex;
  }
  .nav-menu li.mobile-phone-link {
    display: list-item !important; /* Включаем в мобильном меню */
    margin-top: 30px;
    padding-top: 20px;
    border-top: 2px solid var(--accent-color);
  }
  .nav-menu li.mobile-phone-link a {
    color: var(--accent-color);
    font-weight: 700;
    font-size: 1.2rem;
  }
}
.nav-menu a {
  font-weight: 400;
  font-size: 1rem;
  color: var(--primary-color);
  text-decoration: none;
  display: block;
  padding: 10px 0;
  white-space: nowrap;
}
@media (max-width: 992px) {
  .nav-menu a {
    font-size: 1.1rem;
    padding: 12px 0;
    border-bottom: 1px solid #f0f0f0;
  }
  .nav-menu a:last-child {
    border-bottom: none;
  }
}

.header-phone {
  font-weight: 500;
  color: var(--primary-color);
  white-space: nowrap;
  font-size: 1rem;
}
@media (max-width: 992px) {
  .header-phone {
    display: none;
  }
}

.mobile-phone-link {
  display: none;
}
@media (max-width: 992px) {
  .mobile-phone-link {
    display: list-item !important;
    list-style: none;
  }
}

body.menu-open::after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999;
  transition: 0.3s;
}

.nav-menu a {
  position: relative;
}
.nav-menu a:hover::after {
  transform: scaleX(1);
  transform-origin: left center;
}
.nav-menu a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-color: var(--accent-color);
  transform: scaleX(0);
  transform-origin: left center;
  transition: transform 0.3s cubic-bezier(0.55, 0.46, 0.45, 0.94);
}
@media (max-width: 992px) {
  .nav-menu a:hover::after, .nav-menu a::after {
    display: none;
  }
}

footer {
  background: linear-gradient(to right, var(--primary-color), #34495e);
  color: var(--white);
  padding: 70px 0 20px;
  font-size: 0.95rem;
  border-top: 1px solid var(--text-grey);
  text-align: center; /* Центрирование всего содержимого */
}
footer .container {
  display: flex;
  flex-direction: column;
  align-items: center; /* Центрирование контейнера */
}

.footer-grid {
  display: grid;
  grid-template-columns: auto auto auto;
  gap: 80px;
  justify-content: center;
  align-items: start;
  margin: 0 auto;
  width: auto;
  max-width: none;
}
@media (max-width: 992px) {
  .footer-grid {
    gap: 60px;
  }
}
@media (max-width: 768px) {
  .footer-grid {
    grid-template-columns: 1fr;
    gap: 40px;
    text-align: center;
  }
}

.footer-col {
  text-align: left;
  min-width: 250px;
}
@media (max-width: 768px) {
  .footer-col {
    text-align: center;
    min-width: auto;
  }
}
.footer-col .logo-footer {
  margin-top: -1rem;
  margin-bottom: 10px;
  display: inline-block;
  max-width: 150px;
}
.footer-col h4 {
  margin-top: -0.5rem;
  color: var(--white);
  margin-bottom: 25px;
  font-size: 1.3rem;
}
.footer-col ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.footer-col ul li {
  margin-bottom: 12px;
  font-size: 1.2rem;
}
.footer-col ul li a {
  color: var(--white);
  text-decoration: none;
  transition: 0.3s;
}
.footer-col ul li a:hover {
  color: var(--accent-color);
}
.footer-col .text-footer {
  margin-top: 0.8rem;
  max-width: 350px; /* Ограничиваем ширину текста */
  color: var(--white);
  line-height: 1.6;
}
@media (max-width: 768px) {
  .footer-col .text-footer {
    margin-left: auto;
    margin-right: auto;
    max-width: 280px; /* Уменьшаем для мобильных */
  }
}

.footer-bottom {
  width: 100%;
  border-top: 1px solid var(--text-grey);
  margin-top: 40px;
  padding-top: 20px;
  font-size: 1rem;
  opacity: 0.9;
  color: var(--white);
  text-align: center; /* Центрирование текста */
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 60px;
  min-width: 200px;
  padding: 0 40px;
  background-color: var(--accent-color);
  color: var(--white);
  font-weight: 600;
  border-radius: 50px;
  text-transform: uppercase;
  font-size: 1.1rem;
  letter-spacing: 0.5px;
  cursor: pointer;
  border: none;
  box-shadow: 0 4px 15px rgba(230, 172, 0, 0.4);
  transition: 0.3s;
  white-space: nowrap; /* Чтобы текст не переносился */
}
.btn:hover {
  background-color: var(--accent-hover);
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(230, 172, 0, 0.5);
}
.btn.btn-outline {
  background-color: transparent;
  border: 2px solid var(--white);
  color: var(--white);
  margin-left: 15px;
  box-shadow: none;
}
.btn.btn-outline:hover {
  background-color: var(--white);
  color: var(--primary-color);
}
.btn {
  /* АДАПТИВНОСТЬ */
}
@media (max-width: 768px) {
  .btn {
    padding: 0 30px;
    font-size: 0.85rem;
    min-width: 180px;
  }
  .btn.btn-outline {
    margin-left: 0;
    margin-top: 15px;
  }
}
@media (max-width: 480px) {
  .btn {
    padding: 0 25px;
    font-size: 0.8rem;
    min-width: 160px;
    width: 100%; /* На очень маленьких экранах растягиваем */
    max-width: 300px; /* Но не слишком широко */
  }
}

.checkbox-label {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 0.85rem;
  text-align: left;
  margin: 5px 0;
}
.checkbox-label input[type=checkbox] {
  margin-top: 3px;
  flex-shrink: 0;
  accent-color: var(--accent-color);
  width: 16px;
  height: 16px;
}
.checkbox-label a {
  color: var(--accent-color);
  text-decoration: underline;
  font-weight: 600;
}
.checkbox-label a:hover {
  text-decoration: none;
}

.catalog-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* РОВНО 3 */
  gap: 40px;
  max-width: 1350px; /* 3×410 + 2×40 */
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 1400px) {
  .catalog-grid {
    grid-template-columns: repeat(3, 380px);
    gap: 35px;
    max-width: 1240px;
  }
}
@media (max-width: 1300px) {
  .catalog-grid {
    grid-template-columns: repeat(3, 350px);
    gap: 30px;
    max-width: 1150px;
  }
}
@media (max-width: 1200px) {
  .catalog-grid {
    grid-template-columns: repeat(3, 320px);
    gap: 25px;
    max-width: 1060px;
  }
}
@media (max-width: 1100px) {
  .catalog-grid {
    grid-template-columns: repeat(3, 1fr); /* Гибкие размеры */
    gap: 25px;
  }
}
@media (max-width: 992px) {
  .catalog-grid {
    grid-template-columns: repeat(2, 1fr); /* 2 в ряд */
    max-width: 700px;
    gap: 30px;
  }
}
@media (max-width: 768px) {
  .catalog-grid {
    grid-template-columns: 1fr; /* 1 в ряд */
    max-width: 450px;
    gap: 30px;
  }
}
@media (max-width: 480px) {
  .catalog-grid {
    max-width: 100%;
    gap: 25px;
  }
}

.product-card {
  background: var(--white);
  border-radius: var(--radius);
  overflow: hidden;
  box-shadow: var(--shadow-soft);
  transition: 0.4s;
  text-align: center;
  border: 1px solid transparent;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 450px;
  margin: 0;
}
.product-card h3 {
  text-transform: none !important;
  font-size: 1.5rem;
  color: var(--primary-color);
  line-height: 1.3;
}
.product-card .ots {
  font-weight: 600 !important;
  margin-bottom: 20px !important;
}
.product-card:hover {
  transform: translateY(-10px);
  box-shadow: var(--shadow-hover);
  border-color: rgba(230, 172, 0, 0.3);
}
.product-card .card-img-wrap {
  height: 220px;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  border-bottom: 1px solid #eee;
}
.product-card .card-img-wrap img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 0.5s;
}
.product-card:hover .product-card .card-img-wrap img {
  transform: scale(1.1);
}
.product-card .card-content {
  padding: 20px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: var(--white);
}
.product-card .card-content p {
  color: var(--text-grey);
  font-size: 1.3rem;
  margin-bottom: 30px;
  flex-grow: 1;
  line-height: 1.5;
  font-weight: 500;
}
.product-card .card-content .card-link {
  color: var(--accent-color);
  font-weight: 600;
  text-transform: uppercase;
  font-size: 1.3rem;
  letter-spacing: 1px;
  margin-top: auto;
}

@media (max-width: 1100px) {
  .product-card {
    height: 420px;
  }
}
@media (max-width: 992px) {
  .product-card {
    height: 400px;
  }
}
@media (max-width: 768px) {
  .product-card {
    height: auto;
    min-height: 420px;
  }
  .card-img-wrap {
    height: 200px;
  }
}
@media (max-width: 480px) {
  .product-card {
    min-height: 400px;
    margin: 0 auto 20px;
  }
  .card-img-wrap {
    height: 180px;
  }
  .card-content {
    padding: 20px;
  }
}
.hero {
  position: relative;
  height: 85vh;
  min-height: 600px;
  display: flex;
  align-items: center;
  text-align: center;
  color: var(--white);
  overflow: hidden;
  /* Слайдер с 3 фото */
}
.hero .hero-slider {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.hero .slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  opacity: 0;
  animation: slideshow 12s infinite; /* 3 слайда × 4 сек = 12 секунд */
}
.hero {
  /* Задержки для 3 слайдов */
}
.hero .slide:nth-child(1) {
  animation-delay: 0s;
}
.hero .slide:nth-child(2) {
  animation-delay: 4s;
}
.hero .slide:nth-child(3) {
  animation-delay: 8s;
}
.hero {
  /* Анимация для 3 слайдов */
}
@keyframes slideshow {
  0% {
    opacity: 0;
  }
  2.5% { /* 0.3с / 12с ≈ 2.5% - плавное появление */
    opacity: 1;
  }
  33.33% { /* 4с активного показа */
    opacity: 1;
  }
  35.83% { /* Начало исчезания (0.3с) */
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
.hero {
  /* Контент поверх слайдов */
}
.hero .hero-content {
  position: relative;
  z-index: 2;
  max-width: 800px;
  margin: 0 auto;
  animation: fadeInUp 1s ease-out;
  padding: 0 20px; /* Отступы для мобильных */
}
.hero .hero-content h1 {
  font-size: clamp(2rem, 5vw, 3.5rem); /* Адаптивный размер */
  margin-bottom: 20px;
  line-height: 1.1;
  text-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}
.hero .hero-content p {
  font-size: clamp(1rem, 2.5vw, 1.3rem); /* Адаптивный размер */
  margin-bottom: 40px;
  opacity: 0.9;
  color: var(--white);
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.4);
}
.hero .hero-content {
  /* Контейнер кнопок */
}
.hero .hero-content .btn-container {
  display: flex;
  gap: 15px;
  justify-content: center;
  flex-wrap: wrap; /* Перенос кнопок на мобильных */
}
.hero .hero-content .btn-container .btn {
  flex: 1;
  max-width: 300px; /* Максимальная ширина кнопки */
  min-width: 200px; /* Минимальная ширина */
}
@media (max-width: 576px) {
  .hero .hero-content .btn-container {
    flex-direction: column;
    align-items: center;
  }
  .hero .hero-content .btn-container .btn {
    width: 100%;
    max-width: 280px;
  }
}

/* Мобильная версия */
@media (max-width: 768px) {
  .hero {
    min-height: 450px; /* Уменьшаем для мобильных */
    height: 70vh;
  }
  .hero .hero-content {
    padding: 0 15px;
  }
  .hero .hero-content h1 {
    font-size: clamp(1.8rem, 8vw, 2.5rem);
    line-height: 1.2;
  }
  .hero .hero-content p {
    font-size: clamp(0.95rem, 4vw, 1.1rem);
    margin-bottom: 30px;
  }
  .hero .hero-content .btn-container .btn {
    font-size: 0.85rem;
    padding: 12px 25px;
  }
}
@media (max-width: 480px) {
  .hero {
    min-height: 400px;
  }
  .hero .hero-content h1 {
    font-size: clamp(1.5rem, 10vw, 2rem);
  }
  .hero .hero-content p {
    font-size: clamp(0.9rem, 5vw, 1rem);
  }
}
