.catalog {
  padding-top: 80px;
}
@media (max-width: 576px) {
  .catalog {
    padding-top: 60px;
  }
}

.catalog-page-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3 карточки в строке на десктопе */
  gap: 70px;
  margin-top: 60px;
}
@media (max-width: 992px) {
  .catalog-page-grid {
    grid-template-columns: repeat(2, 1fr); /* 2 карточки на планшетах */
  }
}
