  background: var(--accent-color);
  margin: 25px auto 0;
  border-radius: 2px;
}

.section-subtitle {
  text-align: center;
  color: var(--text-grey);
  max-width: 900px;
  margin: 0 auto 60px;
  font-size: 1.3rem;
}

